export default {
  name: "edit.vue",
  layout: "admin",
  data() {
    return {
      selected: null,
      items: [],
      title: "",
      desk: ""
    }
  },
  methods: {
    onChange() {

      const res = this.items.filter(item => item.title === this.selected)

      console.log(res)

      this.title = res[0].title
      this.desk = res[0].desk
    }
  },
  async mounted() {
    this.items = await this.$axios.$get('http://localhost:8080/catalog/getAllItems')
  }
}
